import React from 'react';
import './courseProgram.scss';
const CourseProgram = (props) => {
    return (
        <table>
            <tr>
                <th>Topic</th>
                <th>Nr. of lessons</th>
            </tr>
            {props.topics?.map(topic=>(
                <tr>
                    <td>{topic.topic}</td>
                    <td>{topic.duration}</td>
                </tr>
            ))}
        </table>
    );
}

export default CourseProgram;