import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import "./courseBox.scss";

const CourseBox = (props) => {
    const history = useHistory()
   
    return (
        <div 
        className={props.contStyle === 'visible' ? 'course-box visible' : 'course-box'}
        style = {{...props.addStyle}}
        onClick={()=>history.push(`/course/${props.id}`)}
        >
            <h1 className="course-box__title">{props.title}</h1>
            <img className="course-box__image" src={`courses-img/${props.image}`} alt=""/>
            <p className="course-box__content">{props.content}</p>
            <a className="course-box__more-info" href="">More details</a>
        </div>
    );
}

export default CourseBox;