import React, {useEffect, useRef, useState} from 'react';
import CourseBox from '../components/CourseBox';
import {containers} from '../data/courses';
import './coursesPage.scss'

const CoursesPage = (props) => {

    const [visibleConts, setVisibleConts] = useState([]);
    const [scrolledCont, setScrolledCont] = useState();

    const dynamicRefsCont = useRef([])
    /* SOLUTION FROM  https://stackoverflow.com/questions/54633690/how-can-i-use-multiple-refs-for-an-array-of-elements-with-hooks */
  useEffect(()=>{
    dynamicRefsCont.current = dynamicRefsCont.current.slice(0, containers.length)
  }, [containers])
  useEffect(()=>{
    if(!visibleConts.includes(scrolledCont)){
      setVisibleConts([...visibleConts, scrolledCont])
    }
  },[scrolledCont])

  useEffect(()=>{

    setTimeout(()=>{
      checkBoxesForVisibility()
    },200)
    
    document.addEventListener('scroll', scrollPage);
    return()=>{
      document.removeEventListener('scroll', scrollPage);
    }
  },[])

    const checkBoxesForVisibility = () =>{
      containers.forEach((container,index)=>{
        if(dynamicRefsCont.current[index]?.getBoundingClientRect().top <= window.innerHeight){
          setScrolledCont(container.id)
        }
      })
    }
    const scrollPage = () =>{
      checkBoxesForVisibility()
    }
      return (
          <div className="courses-page">
            <h1 className="courses-page__title">My Completed Udemy Courses</h1>
            <div className="course-boxes-container">
          {containers.map((container,index)=>{
            return(
            <div className="course-box-container"
            key={index} 
            ref={el=>dynamicRefsCont.current[index] = el}
            >
              <CourseBox 
              id={container.id}
              image={container.image}
              title={container.title} 
              content={container.description}
              addStyle={container.style}
              contStyle = {visibleConts.includes(container.id) ? "visible" : null}
              />
            </div>
            )
          })}
            </div>
          </div>
      );
}
export default CoursesPage;