import React, {useEffect, useState} from 'react';
import './counter.scss';
const Counter = (props) => {
    const [number, setNumber] = useState(0);

    useEffect(()=>{

        let counter = 0;

        const numberInterval = setInterval(() => {
        if(counter>= props.maxNumber) return clearInterval(numberInterval);
        counter +=1;
        setNumber(counter);
        }, props.speed);

        return () => {
            clearInterval(numberInterval);
          };

    },[])    
    return (
        <span className="counter">
            <span className="counter-digit">{number}</span> hours
        </span>
    );
}

export default Counter;