import React, {useState, useEffect} from 'react';
import { useHistory } from 'react-router-dom';
import Counter from '../components/counter/Counter';
import CourseProgram from '../components/courseProgram/CourseProgram';
import {selectCourse} from '../data/courses'
import './coursePage.scss'

const CoursePage = (props) => {
    const history = useHistory();
    const [course, setCourse] = useState()

    useEffect(()=>{
        const fetchedCourse = selectCourse(props.match.params.id);
        console.log(fetchedCourse);
        setCourse(fetchedCourse);
        console.log("fetching...");
        console.log("id: " + props.match.params.id)

    }, [props.match.params.id])

    return (
        <div className="single-course-page">
             <div className="single-course-page__image">
                <img src={`/courses-img/${course?.image}`} alt=""/>
             </div>
             <h1 className="single-course-page__title">{course?.details.extendedTitle}</h1>
             <h3 className="single-course-page__instructor">
                <i class="fas fa-chalkboard-teacher"></i>
                 Instructor:  {course?.details.instructor}
            </h3>
             <h3 className="single-course-page__duration">
             <i class="far fa-clock"></i>
                Duration:  { course ? <Counter maxNumber={parseFloat(course?.details.duration)} speed={100}/> : null }
              </h3> 
             <h3 className="single-course-page__description">{course?.details.extendedDescription}</h3>
             <p className="single-course-page__content">{course?.details.content}</p>
             <p className="single-course-page__program">
                 <h3><i class="fas fa-book"></i> Course Program: </h3>
                 <CourseProgram topics={course?.details.program}/>
                 </p>
             <img src="" alt="" className="single-course-page__certificate"/>
             <div className="single-course-page__projects">
                 <p> <i class="fas fa-shapes"></i>  Related Projects</p>
                 <ul>
                     <li>Project 1</li>
                     <li>Project 2</li>
                 </ul>
             </div>
             <a className="single-course-page__back-btn" onClick={()=>history.push('/')}>Back to courses</a>
        </div>
       
    );
}

export default CoursePage;